import { Component, OnInit ,EventEmitter, Output, Input} from '@angular/core';
import { BusinessResourceManageBean } from '../bean/business-resource-manage.bean';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { PerssionManageHttp } from '../../http/perssion-manage.http.service';

@Component({
  selector: 'resource-update',
  templateUrl: './resource-update.component.html',
  styleUrls: ['./resource-update.component.scss']
})
export class ResourceUpdateComponent implements OnInit {
  @Output() outValue:EventEmitter<any>=new EventEmitter();
  @Input() inValue:any;
  businessResourceManageBean: BusinessResourceManageBean = new BusinessResourceManageBean();
  userform: FormGroup; //校验
  //提示信息
  msgs: Message[] = [];
  constructor(
    public fb: FormBuilder,
    public httpService: PerssionManageHttp
  ) { }

  ngOnInit() {
    this.businessResourceManageBean=this.inValue;
    this.userform = this.fb.group({
      'businessEngName': new FormControl('', [Validators.required,Validators.pattern(/^[\w]+$/)]),
      'businessChnName': new FormControl('', [Validators.required,Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
      // 'businessValue': new FormControl('', Validators.required)
    })
  }
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.httpService.modifyBusinessWebAppSource(this.businessResourceManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit({ msgs: '修改成功', display: false })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  cancel() {
    this.outValue.emit({ display: false })
  }
}
