import { Component, OnInit } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { BusinessResourceManageBean } from './bean/business-resource-manage.bean';
import { PerssionManageHttp } from '../http/perssion-manage.http.service';
@Component({
  selector: 'business-resource-manage',
  templateUrl: './business-resource-manage.component.html',
  styleUrls: ['./business-resource-manage.component.scss']
})
export class BusinessResourceManageComponent implements OnInit {
  first: number = 0;
  businessResourceManageBean: BusinessResourceManageBean = new BusinessResourceManageBean();
  applyPerson: string;
  statusId: any[];
  status: string;
  headerTitle: string;
  display: boolean;//弹框
  updateValue: string;//修改传值
  showModel: string;
  tableData: any[];
  total: number;
  //提示信息
  msgs: Message[] = [];
  constructor(
    public confirmationService: ConfirmationService,
    public httpService: PerssionManageHttp
  ) { }

  ngOnInit() {
    this.businessResourceManageBean.pageNum = 1;
    this.businessResourceManageBean.pageSize = 10;
    this.query();
  }


  //查询
  query() {
    this.httpService.queryBusinessWebAppSource(this.businessResourceManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.result.businessWebAppSourceList ? data.result.businessWebAppSourceList : [];
        this.total = data.result.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //点击查询
  queryClick() {
    this.businessResourceManageBean.pageNum = 1;
    this.businessResourceManageBean.pageSize = 10;
    this.first = 0;
    this.query();
  }
  //重置
  reset() {
    this.businessResourceManageBean = new BusinessResourceManageBean();
  }

  //新增
  add() {
    this.headerTitle = '用户审批权限管理新增';
    this.display = true;
    this.showModel = '1';
  }
  //新增回调
  addCall(event) {
    if (event.msgs) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: event.msgs });
      this.query();
    }
    this.display = event.display;
  }
  //修改
  update(item) {
    this.headerTitle = '用户审批权限管理修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item;
    console.log(this.updateValue)
  }
  //修改回调
  updateCall(event) {
    if (event.msgs) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: event.msgs });
      this.query();
    }
    this.display = event.display;
  }
  //删除
  delete(sourceId) {
    //删除
    this.confirmationService.confirm({
      message: '确定要删除吗?',
      accept: () => {

        this.httpService.deleteBusinessWebAppSource({ sourceId }).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        })
      }
    });
  }
  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.businessResourceManageBean.pageSize = rows;
    //当前页
    let currentPage = event.page + 1;
    this.businessResourceManageBean.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.query();
  }
}
